const {Component, Mixin} = Shopware;

import template from './index.html.twig';

const httpClient = Shopware.Application.getContainer('init').httpClient;

Component.register('rhweb-theme-features-demo-data-detail', {
    template,

    inject: [
        'repositoryFactory',
        'acl'
    ],

    mixins: [
        Mixin.getByName('notification'),
        Mixin.getByName('placeholder')
    ],

    data() {
        return {
            item: {
                salesChannelId: null,
                pluginName: null,
                name: null,
            },
            optionIndex: 0,
            options: null,
            isLoading: true,
            processSuccess: false
        };
    },

    created() {
        this.getOptions();
    },

    methods: {
        selectOption(index) {
            if (typeof index == 'undefined') {
                index = this.optionIndex;
            }

            this.item.pluginName = this.options[index].pluginName;
            this.item.name = this.options[index].name;
            this.item.type = this.options[index].type;
        },

        getOptions() {
            this.isLoading = true;

            httpClient({
                method: 'get',
                url: '/rhweb-theme-features/settings/demo-data/options',
                responseType: 'json'
            }).then((response) => {
                this.options = response.data;
                this.selectOption(0);
                this.isLoading = false;
            }).catch((error) => {
                this.createNotificationError({
                    title: this.$tc('global.default.error'),
                    message: error,
                });
                this.isLoading = false;
            });
        },

        install() {
            this.isLoading = true;

            httpClient({
                method: 'post',
                url: '/rhweb-theme-features/settings/demo-data/install',
                responseType: 'json',
                data: this.item
            }).then((response) => {
                this.createNotificationSuccess({
                    message: this.$tc('rhweb-theme-features.notification.demoDataInstalled')
                });
                this.isLoading = false;
            }).catch((error) => {
                this.createNotificationError({
                    title: this.$tc('global.default.error'),
                    message: error,
                });
                this.isLoading = false;
            });
        },

        remove() {
            this.isLoading = true;

            httpClient({
                method: 'post',
                url: '/rhweb-theme-features/settings/demo-data/remove',
                responseType: 'json',
                data: this.item
            }).then((response) => {
                this.createNotificationSuccess({
                    message: this.$tc('rhweb-theme-features.notification.demoDataRemoved')
                });
                this.isLoading = false;
            }).catch((error) => {
                this.createNotificationError({
                    title: this.$tc('global.default.error'),
                    message: error,
                });
                this.isLoading = false;
            });
        }
    }
});
