const {Component} = Shopware;
const Criteria = Shopware.Data.Criteria;

import template from './sw-cms-slot.html.twig';
import './sw-cms-slot.scss';

Component.override('sw-cms-slot', {
    template,

    inject: [
        'repositoryFactory'
    ],

    data() {
        return {
            snippetPrefix: 'sw-cms.component.sw-cms-slot.',
            showRhwebCmsElementAnimations: false
        };
    },

    computed: {

    },

    methods: {
        onRhwebCmsElementAnimationsButtonClick() {
            this.showRhwebCmsElementAnimations = true;
        },

        onCloseRhwebCmsElementAnimationsModal() {
            this.showRhwebCmsElementAnimations = false;
        }
    }
});
