const {Component, Mixin} = Shopware;

import template from './index.html.twig';
import './index.scss';

Component.register('rhweb-element-animation', {
    template,

    mixins: [
        Mixin.getByName('cms-element')
    ],

    data() {
        return {
            snippetPrefix: 'rhweb-element-animation.',
        };
    },

    created() {
        this.createdComponentExtra();
    },

    computed: {
        rhwebFoundation() {
            return RHWebFoundation;
        }
    },

    methods: {
        async createdComponentExtra() {
            const extraConfig = {
                rhwebAnimation: {
                    source: 'static',
                    value: {
                        cssSelector: null,
                        in: {
                            active: false,
                            name: 'none',
                            condition: 'isOverBottom',
                            duration: 1000,
                            delay: 0,
                        },
                        out: {
                            active: false,
                            name: 'none',
                            condition: 'isOverBottom',
                            duration: 1000,
                            delay: 0,
                        },
                        hover: {
                            active: false,
                            name: 'none',
                            condition: 'isOverBottom',
                            duration: 1000,
                            delay: 0,
                        }
                    }
                }
            };

            this.element.config = Object.assign(extraConfig, this.element.config);
        }
    }
});
