const {Module} = Shopware;

import './detail';
import './index.scss';

Module.register('rhweb-theme-features-demo-data', {
    type: 'plugin',
    name: 'rhweb-theme-features-demo-data',
    title: 'rhweb-theme-features.navigation.demoData',

    routes: {
        detail: {
            component: 'rhweb-theme-features-demo-data-detail',
            path: 'detail',
            meta: {
                parentPath: 'sw.settings.index'
            },
        }
    },

    settingsItem: [
        {
            privilege: 'system.system_config',
            name: 'rhweb-theme-features-demo-data-detail',
            to: 'rhweb.theme.features.demo.data.detail',
            group: 'plugins',
            icon: 'default-action-cloud-download',
            label: 'rhweb-theme-features.navigation.demoData'
        }
    ]
});
